<template>
  <div class="DashboardUsers">

    <!-- ### Filters Toolbar Start ### -->
    <div class="toolbar">
      <div class="group">
        <el-input
          v-model.trim="filterQuery"
          class="filter filter-query"
          placeholder="ФИО, email ..."
          size="small" />
        <el-select
          v-model="filterRole"
          class="filter filter-role"
          size="small"
          multiple
          collapse-tags
          placeholder="Все роли">
          <el-option
            v-for="option in filterRoleOptions"
            :key="option.value"
            :label="option.label"
            :disabled="option.disabled"
            :value="option.value" />
        </el-select>
      </div>
      <div class="group">
        <el-button
          size="small"
          type="primary"
          @click="newUserDialog = true">Создать Пользователя</el-button>
      </div>
    </div>
    <!-- ### Filters Toolbar End ### -->

    <!-- ### Container Start ### -->
    <div
      class="container">
      <el-table
        :show-header="false"
        :data="filteredUsers"
        highlight-current-row
        style="width: 100%"
        @row-click="userShow">
        <el-table-column
          prop="id"
          label="#" />
        <el-table-column
          label="Имя">
          <template slot-scope="scope">
            {{ scope.row.first_name }} {{ scope.row.last_name }}
          </template>
        </el-table-column>
        <el-table-column
          prop="email"
          label="Email" />
        <el-table-column
          align="right"
          label="">
          <template slot-scope="scope">
            <el-tag
              v-if="scope.row.id === current_user.id"
              type="success"
              disable-transitions>Вы</el-tag>
            <el-tag
              v-if="scope.row.admin"
              disable-transitions>Админ</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- ### Container End### -->

    <!-- ### newUserDialog Start ### -->
    <el-dialog
      :visible.sync="newUserDialog"
      title="Создать пользователя">
      <el-form :model="newUser">
        <el-form-item
          label-width="80px"
          label="Email">
          <el-input
            v-model="newUser.email"
            autocomplete="off" />
        </el-form-item>
        <el-form-item
          label-width="80px"
          label="Пароль">
          <el-input v-model="newUser.password" />
        </el-form-item>
        <el-form-item label="Администратор">
          <el-switch
            :disabled="true"
            v-model="newUser.admin" />
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer">
        <el-button @click="newUserDialog = false">Отменить</el-button>
        <el-button
          type="primary"
          @click="createUser">Создать</el-button>
      </span>
    </el-dialog>
    <!-- ### newUserDialog End ### -->

  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'DashboardUsers',
  data() {
    return {
      users: [],
      filterQuery: '',
      filterRole: ['admin'],
      filterRoleOptions: [
        { value: 'manager', label: 'Менеджер', disabled: true },
        { value: 'stats', label: 'Статист', disabled: true },
        { value: 'admin', label: 'Админ' },
      ],
      newUserDialog: false,
      newUser: {
        email: '',
        password: '',
        password_confirmation: '',
        admin: true,
      },
    };
  },

  computed: {
    ...mapState('auth', ['current_user']),

    filteredUsers() {
      const keyword = this.filterQuery.toLowerCase();
      let filtered = this.users;

      // Временно фильтруем дефолтно только на атрибут admin
      if (this.filterRole.length > 0) {
        filtered = filtered.filter(u => u.admin);
      }

      return filtered.filter(user => user.first_name?.toLowerCase().includes(keyword)
        || user.last_name?.toLowerCase().includes(keyword)
        || user.second_name?.toLowerCase().includes(keyword)
        || user.email?.toLowerCase().includes(keyword));
    },
  },

  mounted() {
    this.getUsers();
  },

  methods: {
    async getUsers() {
      const response = await this.$http.get('/users');
      this.users = response.data;
    },

    userShow(row) {
      this.$router.push({ name: 'DashboardUsersShow', params: { id: row.id } });
    },

    async createUser() {
      this.newUser.password_confirmation = this.newUser.password;
      await this.$http.post('users', { user: this.newUser });
      this.newUserDialog = false;
      this.getUsers();
      this.$message.success('Пользователь был успешно создан');
    },
  },
};
</script>

<style lang="scss" scoped>
  .DashboardUsers {
    display: grid;
    grid-template-rows: 60px calc(100vh - 120px);
    height: 100%;
    .toolbar {
      .filter-query {
        width: 170px;
      }
      .filter-role {
        label {
          margin: 0;
        }
      }
    }

    .container {
      overflow: auto;
      height: inherit;
      padding-bottom: 15%;

      .el-tag {
        margin-left: 0.5rem;
      }
    }
  }
</style>
